

import lazyLoad from "&/lazyload";
import { Route, Switch, Redirect } from "react-router-dom";
import React from 'react';
const Guide = lazyLoad(() => import("./guide"));
const Index = lazyLoad(() => import("./boss"));
const Search = lazyLoad(() => import("./search"));
const Scan = lazyLoad(() => import("./scan"));
const Login = lazyLoad(() => import("./login"));
const Otherlogin = lazyLoad(() => import("./login/otherlogin"));
const Register = lazyLoad(() => import("./register"));
const Findmore = lazyLoad(() => import("../components/findmore"));
const Detail = lazyLoad(() => import("./detail"));
import Gotocar from "./gotocar";
import Gotocheck from "./gotocheck";

export class Layout extends React.Component {
    render() {
        return (
            <div className="main">
                <Switch>
                    <Route path="/" exact render={() => <Redirect to="/guide" />} />
                    <Route path="/guide" component={Guide} />
                    <Route path="/index/" component={Index} />
                    <Route path="/search" component={Search} />
                    <Route path="/scan" component={Scan} />
                    <Route path="/login" component={Login} />
                    <Route path="/otherlogin" component={Otherlogin} />
                    <Route path="/findmore" component={Findmore} />
                    <Route path="/register" component={Register} />
                    <Route path="/detail" component={Detail} />
                    <Route path="/gotocar" component={Gotocar} />
                    <Route path="/gotocheck" component={Gotocheck} />
                </Switch>
            </div>
        )
    }
}












