

import { history } from '&/history';
import { Modal, NavBar, Icon, Popover, Checkbox, Flex, List, Stepper } from 'antd-mobile';
const alert = Modal.alert;
export default class Gotocheck extends Component {
    constructor() {
        super();
    }
    componentDidMount() {
        function color16() {//十六进制颜色随机
            var r = Math.floor(Math.random() * 256);
            var g = Math.floor(Math.random() * 256);
            var b = Math.floor(Math.random() * 256);
            var color = '#' + r.toString(16) + g.toString(16) + b.toString(16);
            return color;
        }
        var timer = setInterval(function () {
            if (document.querySelector(".box")) {
                var box = document.querySelector(".box");
                box.style.color = color16();
            }
        }, 2000);
    }
    goback = () => {
        const alertInstance = alert('pay', '你真的打算不买了吗', [
            { text: 'Cancel', onPress: () => console.log('cancel'), style: 'default' },
            { text: 'OK', onPress: () => history.go(-1) }
        ]);
        setTimeout(() => {
            alertInstance.close();
        }, 5000);

    }
    render() {
        return (
            <div style={{ overflow: 'hidden' }}>
                <marquee style={{ zIndex: 100, position: 'fixed', fontSize: '30px', color: "red", top: '50px' }} height="200" direction="down" behavior="alternate">
                    <div className="box">
                        &nbsp;&emsp;&ensp;你需要支付￥：
                    {new URLSearchParams(this.props.location.search).get("money")}
                    </div>
                </marquee>
                <img style={{ width: '100%', margin: '0 auto' }} src={require("@/assets/images/pay.jpg")} />
                <button style={{ zIndex: 2, width: '60px', height: '30px', position: 'fixed', top: "10px", left: "10%" }}
                    onClick={() => this.goback()}>返回</button>
            </div>
        )
    }
}