

import App from '../App';
import store from "./store";
import { render } from "react-dom";
export default class ReduxDemo extends Component {
    render() {
        return (
            <div>
                <App></App>
            </div>
        )
    }
}

const hotRender = () => {
    render(
        <ReduxDemo></ReduxDemo>,
        document.getElementById('root')
    )
}
hotRender();
store.subscribe(hotRender);  // 订阅 state 每当state 改变 会重新 刷新 render 